import Image from "next/image";

interface Preferences {
  language?: string;
  ridePreference?: string;
}

interface User {
  name: string;
  email: string;
  bio: string;
  profilePic: string;
  preferences: Preferences;
  rating?: number;
  ridesTaken?: number;
}

export default function Profile({ user }: { user: User }) {
  return (
    <div className="bg-white p-6 rounded-lg shadow-lg w-full max-w-md text-center">
      <h2 className="text-xl font-bold mb-4">Edit Your Profile</h2>

      {/* Profile Picture */}
      <div className="w-24 h-24 mx-auto mb-4">
      <Image
  src={user?.profilePic && user.profilePic !== "" ? user.profilePic : "/default-avatar.png"}
  alt="Profile Picture"
  width={100}
  height={100}
  unoptimized={true}  // Optional: disable Next.js image optimization if needed
/>

      </div>

      {/* Name Input */}
      <input
        type="text"
        defaultValue={user.name}
        className="w-full px-4 py-2 border rounded-md mb-2"
      />

      {/* Bio Input */}
      <textarea
        defaultValue={user.bio}
        className="w-full px-4 py-2 border rounded-md mb-2"
      ></textarea>

      {/* Email (Read-only) */}
      <input
        type="email"
        defaultValue={user.email}
        className="w-full px-4 py-2 border rounded-md mb-2 bg-gray-200 cursor-not-allowed"
        readOnly
      />

      {/* Save Button */}
      <button className="w-full bg-blue-500 text-white py-2 rounded-md mt-2">
        Save Changes
      </button>

      {/* Back Button */}
      <button className="w-full bg-gray-500 text-white py-2 rounded-md mt-2">
        Back to Dashboard
      </button>
    </div>
  );
}
